.ready__job {
    display: inline-block;
    width: 180px;
    text-align: center;
    padding: 10px 10px 9px;
    margin: 15px 40px;
    position: relative;
    transition: all 0.7s;
    background: #ccc;

}
//  #53377A насыщенный фиолетовый
// rgba(83,55,122,1);
// #641C34;

.ready__job:before {
  z-index: 2;
  content: '';
  position: absolute;
  width: 51%;
  height: 100%;
  top: 0;
  left: 0;
  /*// border: 1px solid #42a4ff;*/
  // background-color: #1142aa;
  background-color: #53377A;
  border-top-left-radius: 1px;
  border-bottom-left-radius: 1px;
  transition: all 0.7s;
}
.ready__job:after {
  z-index: 2;
  content: '';
  position: absolute;
  width: 50%;
  height: 100%;
  top: 0;
  right: 0;
  /*// border: 1px solid #42a4ff;*/
  // background-color: #1142aa;
   background-color: #53377A;
  border-top-right-radius: 1px;
  border-bottom-right-radius: 1px;
  transition: all 0.7s;
}
.ready__job span {
  position: relative;
  font-weight:600;
  z-index: 3;
  color: #ffffff;
  transition: all 0.7s;
}
.ready__job:hover {
  background-color: rgba(17, 66, 170, 0.2);
}
.ready__job:hover:before {
  left: -18px;
  width: 12%;
  background-color:  #53377A;
}
.ready__job:hover:after {
  right: -18px;
  width: 12%;
  background-color:  #53377A;
}
.ready__job:hover span {
  color: rgba(0, 0, 0, 0.8);
}


.grey {
    &::before,
    &::after {
        background-color: #555555;
        opacity: 0.8;
    }
}
